* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #fff;
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 400;
}

.panel {
    background-color: #fff;
    padding: 2rem;
    margin: 1rem;
    border: 1px solid #ccc;
}
.floating-header {
    display: inline-block;
    margin: 1rem;
    padding: .5rem;
    background-color: rgba(255,255,255,.8);
    position: absolute;
    border-radius: 20px;
}
.logo {
    margin: 0 auto 2rem;
    display: block;
    max-height: 14vh;
}
.header {
    padding: 0 1rem;
    background-color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.header .title {
    -ms-flex-positive: 1;
        flex-grow: 1;
}
.header .login-button,
.header .logout-button {
    float: right;
}
.header .login-button {
    margin: 1rem;
}
.header .avatar {
    height: 36px;
    width: 36px;
    border-radius: .5rem;
    background-size: contain;
    top: 13px;
    position: relative;
}
.header .buttons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.header .add-link {
    margin: 10px;
}

#my-links {
    padding: 2rem;
    min-height: 100vh;
}
.my-links-header {
    text-align: center;
}
.my-link {
    border: 4px solid #e25a5a;
    color: #e25a5a;
    padding: 1rem;
    margin: 1rem auto 0;
    font-size: 1.75rem;
    cursor: pointer;
    max-width: 44vw;
}

.my-link .subtitle {
    font-size: .9rem;
}

.add-new-item {
    margin-top: 1rem;
}

.action-button {
    display: block;
    clear: both;
    text-align: right;
}

.form-element-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.form-element-wrapper .form-element {
    margin: .5rem 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: baseline;
        align-items: baseline;
}
.form-element-wrapper .form-element.medium {
    width: 50%;
}
.form-element-wrapper .form-element.wide {
    width: 100%;
}
.form-element-wrapper .form-element > label {
    margin-right: 1rem;
    width: 20%;
    text-align: right;
}
.form-element .input {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.prefs-preview {
    border: 1px solid #222;
    margin-top: 2rem;
    padding: 3rem 1rem;
    text-align: center;
}

.prefs-preview .avatar {
    width: 120px;
    height: 120px;
}



